@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,400;0,500;0,600;1,400&display=swap');
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.6.0/css/all.min.css);

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 16px;
    background-color: #f5f4f4;
  }

  .container{
    max-width: 1000px;
    margin: auto;
  }

// Global flex classes
.flex {
    display: flex;
   
    &.flex-wrap {
      flex-wrap: wrap;
    }
   
    &.flex-center {
      align-items: center;
      justify-content: center;
    }
   
    &.flex-between {
      justify-content: space-between;
    }
   } 
   
   .gap-25 {
     gap: 25px;
   }
   
   .br-10 {
     border-radius: 10px;
   }
   
   img{
     max-width: 100%;
     object-fit: cover;
    
   }
   
h2 {
    font-size: 32px;
    margin-bottom: 18px;
  }
  section{
    margin: 100px 0;
 } 




// Hero CSS
.hero {
    position: relative;
    height: 70vh;
    overflow: hidden;
  
    img {
      width: 100%;
      transform: translate(0,-180px);
    }
  
    &-content {
      max-width: 450px;
      position: absolute;
      right: 100px;
      top: 50%;
      transform: translateY(-50%);
  
      h2{
        margin-bottom: 20px;
        text-shadow: 2px 1px 1px white;
        color: white;
        font-size: 42px;
        
        } 
      p {
        color: white;
         text-shadow: 1px 1px 7px white;
         line-height: 30px;
         
      }
    }
  }
  


// Special Dishes CSS
.special-dishes {

    &-content {
      max-width: 780px;
      margin: auto;
    
    }
    h2{
  
        text-align: center;
        font-size: 42px;
        animation: animate 25s alternate 2;
        
        
    

    }
    p{
        line-height: 30px;
        text-align:center;
        
    }
  
    &-list {
      margin-top: 60px;
  
      h5 {
        margin-top: 18px;
      }
  
      li {
        width: 22%;
        position: relative;
        overflow: hidden;
        
  
        &::before {
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100px;
          border-radius: 10px;
          background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.65) 40%);
         
        }
  
        &:hover {
          h5 {
            animation: animate 10s alternate 2;
          }
        } 
  
        @keyframes animate {
          0% {
            transform: translateX(0%);
          }
          50% {
            transform: translateX(-50%);
          }
          100% {
            transform: translateX(0%);
          }
        
        }
  
        h5 {
          position: absolute;
          bottom: 0;
          color: white;
          padding: 0 10px;
          white-space: nowrap;
        }
      }
    }
  }




  // Header CSS
header {
  position: absolute;
  z-index: 1;
  width: 100%;
  padding: 7px 50px;
  background-color: rgba(73,69,48,0.3);
  

  .logo {
    background-color: #ffb902;
    color: rgb(34, 33, 33);
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: bold;
    border: 5px solid rgba(255,255,255, 0.5);
    text-decoration: none;
  }

  nav {
    li{
      margin: 0 30px;
      list-style: none;
      

      &:hover {
        a{
          color: #ff4848;
        }
      }
    }
    a {
      color: #ffb902;
      font-size: 18px;
      font-weight: 500;
      padding: 40px;
      transition: color ease 0.3s;
      text-decoration: none;
      
    }
  }
}



// Filtered dishes
.filtered-dishes {
  p{
    text-align: left;
    max-width: 700px;
    text-align: justify;
  }
    ul {
      display: flex;
      flex-wrap: wrap;
      margin-top: 30px;
      
     
      
  
      li {
        background-color: #ffb902;
        color: white;
        padding: 12px 30px;
        border-radius: 30px;
        margin-right: 20px;
        margin-bottom: 20px;
        cursor: pointer;
        font-weight: bold;
        transition: all ease 0.3s;
        list-style: none;
     
        
       
  
        &.active {
          background-color: black;
        }
  
        &:hover {
          background-color: black;
        }
      }
    }
  }
  
  .filtered-dishes-results {

    
    
    margin: auto;
    margin-top: 60px;
    
    
  
      h5 {
        margin-top: 18px;
        
     
    
      }
  
      li {
        width: 22%;
        position: relative;
        overflow: hidden;
        
      }
      img{
        border-radius: 30px;
        
      }
  }
// Loader
.loader {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#000, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  
  

  h1 {
    color: white;
  }
}
.footer{
  padding: 20px;
  background-color:  dimgrey;
}
  h3{
    text-align: center;
    display: flex;
    justify-content: center;
    color: white;
    font-size: 14px;
  }


  .alert{
    border: 2px solid orange;
    border-radius: 10px;
    height: 20vh;
    width: 100%;
    margin-bottom: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


    h3{
      color:red;
      margin-bottom: 10px;
    }
    h4{
      margin: 0;
    }

  }
  
// Pagination
.pagination {
  justify-content: center;
  margin: 50px 0 0;


  li {
    width: 30px;
    height: 30px;
    border: 1px solid gray;
    margin: 0 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 8px;
    transition: all ease 0.3s;
    border-radius: 10px;

    &:hover {
      background-color: #ffb902;
      border-color: #ffb902;
    }
  }
}
